<template>
  <div class="main">
    <div class="loginBox">
      <div class="title">
        Login
      </div>
      <div class="container">
        <w-input
          v-model="username"
          type="email"
          name="email"
          placeholder="Username"
          :disable="loading"
        />
        <w-input
          v-model="password"
          type="password"
          name="password"
          placeholder="Password"
          :disable="loading"
          @enter="signIn"
        />
        <div class="signupText">
          To create an account, go to
          <pre>https://forum.plutonium.pw/register</pre>
        </div>
        <div class="actions">
          <w-btn
            label="Sign in"
            :disable="!canContinue"
            @click="signIn"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: '',
      remember: true
    }
  },
  computed: {
    ...mapGetters({
      loading: 'ui/showLoadingModal'
    }),
    canContinue () {
      return this.username && this.password && !this.loading
    }
  },
  beforeCreate () {
    this.$store.dispatch('ui/setLoadingState', { show: true, message: 'Checking for saved credentials...' })
  },
  mounted () {
    if (process.env.VUE_APP_ENV === 'development') {
      this.username = process.env.VUE_APP_DEV_USER
      this.password = process.env.VUE_APP_DEV_PASS
    }
    if (!this.$route.query.logout) {
      this.$nextTick(() => {
        try {
          // eslint-disable-next-line no-undef
          const tokenExists = isTokenAvailable()
          if (tokenExists) this.$store.dispatch('authentication/preAuthed')
          else this.$store.dispatch('ui/setLoadingState', { show: false })
        } catch (error) {
          console.error('error while trying to check if nix is logged in. Probably because nix isn\'t ready yet.')
          this.$store.dispatch('ui/setLoadingState', { show: false })
        }
      })
    } else this.$store.dispatch('ui/setLoadingState', { show: false })
  },
  methods: {
    signIn () {
      if (this.username && this.password) {
        this.$store.dispatch('authentication/authenticate', {
          username: this.username,
          password: this.password,
          remember: this.remember
        })
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.main {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.loginBox {
  background-color: $primary-2;
  min-width: 500px;
  width: 750px;
  max-width: 50%;
  max-height: 50%;
  box-shadow: 2px 2px 2px 0px alpha(black, 0.1);
  border-radius: 5px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  left: 50%;

  .title {
    text-transform: uppercase;
    background-color: $primary-3;
    padding: 12px 16px 12px 16px;
    font-family: politica-expanded, sans-serif;
    font-weight: 700;
    box-shadow: 0 2px 2px 0px alpha(black, 0.1);
  }

  .container {
    margin: 4px;
  }

  .actions {
    float: right;
  }
}

.signupText {
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: -25px;
  margin-left: 8px;
  font-size: 15px;
  color: alpha(white, 0.3);

  pre {
    display: inline;
    padding: 0;
    margin: 0;
  }
}
</style>
